.sign-out {
  background-image: url(../images/textures/wood3.png);
  border: none;
  color: black;
  padding: 0.3rem 0.4rem;
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  filter: drop-shadow(0 0 2px rgba(0, 0, 0, 1))
  drop-shadow(0 0 5px rgba(0, 0, 0, 1))
  drop-shadow(0 0 10px rgba(0, 0, 0, .5));
}

.logo-rel {
  position: relative !important;
  filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.75));
}

.side-container {
  position: absolute;
  min-width: 30rem;
  height: 100vh;
  background: rgb(32, 32, 40);
  background-image: url(../images/textures/wood5.png);
  filter: drop-shadow(0px 0px 10px rgb(0, 0, 0));
  display: flex;
  flex-direction: column;
  color: white;
}

.side-container hr {
  width: 100%;
  border: 0.5px solid rgba(70, 70, 80, 0.5);
  margin-bottom: 0rem;
}

.side-container svg {
  color: white;
}

.feed-nav {
  min-height: 4rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background: rgb(42, 42, 55);
  background-image: url(../images/textures/wood5.png);
  filter: drop-shadow(0 0 2px rgba(0, 0, 0, 1))
  drop-shadow(0 0 5px rgba(0, 0, 0, 1))
  drop-shadow(0 0 15px rgba(0, 0, 0, .5));
}

.feed-container {
  margin: 1.5rem 2rem;
  padding: 0rem 0rem;
  height: 50%;
  filter: drop-shadow(0 0 2px rgba(0, 0, 0, 1))
  drop-shadow(0 0 5px rgba(0, 0, 0, 1))
  drop-shadow(0 0 10px rgba(0, 0, 0, 1));
}

.feed {
  background: rgb(37, 38, 44);
  height: 100%;
  border-radius: 10px;
  border-top-left-radius: 0px;
  overflow-x: hidden;
  overflow-y: scroll;
}

.feed-button {
  padding: .25rem 1rem;
  border-radius: 0px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background: rgb(42, 42, 55);
  background-image: url(../images/textures/wood3.png);
  border: none;
  z-index: 5;
  margin-right: .5rem;
}

.feed-button-inactive {
  background: rgb(34, 34, 38);
  background-image: url(../images/textures/wood4.png);
}

.thread-card {
  flex-direction: row;
  align-items: center;
  padding: 1rem 1rem;
  background: rgb(42, 42, 55);
  background-image: url(../images/textures/wood3.png);
  border-radius: 0px;
  filter: none;
}

.wood1 {
  background-image: url(../images/textures/wood2.png);
}

.wood2 {
  background-image: url(../images/textures/wood3.png);
}

.thread-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.thread-card h2 {
  cursor: pointer;
  font-family: "Nunito", sans-serif;
  font-size: 1rem;
  font-weight: lighter;
  line-height: 1.4rem;
  color: white;
}

.thread-card h2 {
  font-weight: lighter;
  color: white;
  text-shadow: 0 0 5px black;
}

.thread-card h2:hover {
  text-decoration: overline;
}

.thread-card a {
  color: rgb(240, 240, 240);
}

.thread-card a:hover {
  text-decoration: underline;
}

.thread-card p {
  font-size: 0.75rem;
  line-height: 0.75rem;
  color: rgb(220, 220, 220);
  text-shadow: 0 0 5px black;
}

.thread-card button {
  background: none;
  border: none;
  font-size: 0.75rem;
  line-height: 1rem;
  padding: 0 0;
  margin: 0 0;
  margin-right: 0.5rem;
  color: white;
  text-shadow: 0 0 5px black;
  -webkit-touch-callout: auto;
  -webkit-user-select: auto;
  -khtml-user-select: auto;
  -moz-user-select: auto;
  -ms-user-select: auto;
  user-select: auto;
}

.thread-card button:hover {
  text-decoration: underline;
}

.thumb {
  overflow: hidden;
  text-align: center;
  width: 60px;
  height: 60px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.thumb img {
  vertical-align: middle;
  display: -moz-inline-box; /* Firefox 2 */
  display: inline-block;
}

.gmap {
  width: 100%;
  height: 100vh;
}

::-webkit-scrollbar {
  display: none;
}

/* .gmnoprint a {
  color: white!important;
} */

.feed-search {
  font-family: "Nunito", sans-serif;
  font-size: .9rem;
  border: none;
}

.feed-search:focus {
  box-shadow: 0 0 0px 0px rgb(0, 0, 0);
}

::-webkit-input-placeholder {
  font-family: "Nunito", sans-serif;
  color: rgb(225, 225, 255);
}
:-moz-placeholder {
  font-family: "Nunito", sans-serif;
  color: rgb(225, 225, 255);
}
::-moz-placeholder {
  font-family: "Nunito", sans-serif;  
  color: rgb(225, 225, 255);
}
:-ms-input-placeholder {
  font-family: "Nunito", sans-serif;
  color: rgb(225, 225, 255);
}
