h1 {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

a {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.wlogo {
  width: 2rem;
  height: 2rem;
}

.transition-up {
  transition: all 150ms ease-out 0s;
}

.transition-up:hover {
  transform: translateY(-1.5px);
}

.transition-up:active {
  transform: translateY(0px);
}

.landing-nav {
  max-width: 1300px;
  min-height: 3.5rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}

.mobile-nav {
  display: none;
  min-height: 3.5rem;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}

@media only screen and (max-width: 768px) {
  .landing-nav {
    display: none;
  }

  .mobile-nav {
    display: flex;
  }
}

@media only screen and (max-width: 600px) {
  h2 {
    padding: 0;
    margin: 0;
    font-family: "Raleway", sans-serif;
    font-size: 1.4rem;
    font-weight: bolder;
    color: white;
  }
}

.headline {
  width: 30rem;
}

@media only screen and (max-width: 600px) {
  .headline {
    width: fit-content;
    font-size: 0.8rem;
    text-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
  }
}

svg {
  cursor: pointer;
  color: rgb(169, 178, 201);
}

svg:hover {
  color: white;
}

.active {
  color: white;
}

.hline {
  padding-bottom: 0.3rem;
  color: rgb(128, 136, 155);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.button-group {
  display: flex;
  flex: row;
}

@media only screen and (max-width: 600px) {
  .button-group {
    display: flex;
    flex-direction: column;
  }

  .button-group button {
    margin-bottom: .75rem;
  }
}

button {
  font-family: "Nunito", sans-serif;
  font-size: 0.8rem;
  padding: 0.5rem;
  color: white;
  border-radius: 3px;
  border: 1px solid rgb(20, 170, 140);
  background: rgb(0, 170, 132);
  cursor: pointer;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.fill-button {
  border-radius: 5px;
  filter: drop-shadow(0 0 8px rgba(0, 0, 0, .75));
}

.fill-button:hover {
  background: rgb(10, 190, 145);
}

.outline-button {
  font-size: 0.8rem;
  line-height: 1.1rem;
  padding-bottom: 0.25rem;
  padding-top: 0.25rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border-radius: 20px;
  background: none;
  border: 1.5px solid rgb(169, 178, 201);
  color: rgb(204, 209, 223);
}

@media only screen and (max-width: 768px) {
  .button-icon {
    display: none;
  }
}

.outline-button:hover {
  color: white;
  border-color: white;
}

.outline-button:hover svg {
  color: white;
}

.outline-button:focus {
  outline: none;
  color: white;
  border-color: white;
}

.outline-button:focus svg {
  color: white;
}

.content-section {
  max-width: 1000px;
  min-height: 48rem;
  display: flex;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin: 0rem auto;
  padding: 0rem 0rem;
}

@media only screen and (max-width: 768px) {
  .content-section {
    min-height: 40rem;
    padding: 4rem 1rem!important;
    overflow: hidden;
  }
}

.map-img {
  height: 42rem;
  position: relative;
  margin-top: -3.5rem;
  margin-left: -2rem;
  z-index: -1;
  display: block;
}

@media only screen and (max-width: 768px) {
  .map-img {
    height: 34rem;
    margin-top: -5rem;
    margin-left: -1rem;
  }
}

footer {
  display: block;
  width: 100%;
}

.footer-menu {
  text-align: left;
  max-width: 1200px;
  display: flex;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  align-items: flex-start;
  justify-content: center;
  margin: 0rem auto;
  padding: 0rem 0rem;
}

.footer-nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 50%;
}

.flex-spacer {
  flex-grow: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

ul li {
  height: 1.1rem;
}

ul a {
  cursor: pointer;
  font-family: "Nunito", sans-serif;
  font-size: 0.8rem;
}

.socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 15%;
}

.med-icon {
  font-size: 1.25rem;
}

.socials svg {
  font-size: 2rem;
  margin-left: 0.5rem;
  margin-bottom: 0.75rem;
}

hr {
  width: 25%;
  border: 1px solid rgba(169, 178, 201, 0.3);
  margin-top: 0rem;
  margin-bottom: 1.5rem;
}
